:root {
  --header-background-color: #fff;
  --header-dark-background-color: #111;
  --main-background-color: #f6f6f6;
  --main-dark-background-color: #202024;
  --inactive-background-color: #1116;
  --primary-font-color: #000;
  --secondary-font-color: #4f2ee8;
  --tertiary-font-color: #11111180;
  --quaternary-font-color: #fff;
  --quinary-font-color: #fff;
  --senary-font-color: #0009;
  --septenary-font-color: #f3f3f3;
  --nonary-font-color: #ffffff80;
  --denary-font-color: #111;
  --tenth-font-color: #b4afaf;
  --primary-border-color: #111;
  --secondary-border-color: #4f2ee8;
  --tertiary-border-color: #4f2ee866;
  --quaternary-border-color: #fff;
  --quinary-border-color: #4f2ee84d;
  --senary-border-color: #111;
  --primary-icon-color: #fff;
  --secondary-icon-color: #111;
  --primary-object-background-color: #eac645;
  --secondary-object-background-color: #4f2ee8;
  --tertiary-object-background-color: #f6f6f6;
  --quaternary-object-background-color: #fff;
  --quinary-object-background-color: #202024;
  --primary-buttonsign-background-color: #111;
  --category-on-focus-color: #4f2ee8;
  --scrollbar-color: #e0e0e0;
}

.dark-theme {
  --header-background-color: #111;
  --main-background-color: #202024;
  --primary-border-color: #fff;
  --primary-font-color: #f3f3f3;
  --secondary-icon-color: #fff;
  --denary-font-color: #fff;
  --secondary-icon-color: #fff;
  --primary-buttonsign-background-color: #f6f6f6;
  --septenary-font-color: #202024;
  --tertiary-font-color: #fff9;
  --category-on-focus-color: #eac645;
}

html {
  max-width: 1440px;
  margin: 0 auto;
}

body {
  background-color: var(--main-background-color);
  color: var(--denary-font-color);
  margin: 0 auto;
}

* {
  margin: 0;
  padding: 0;
  font-family: DM Sans, sans-serif;
}

ul {
  list-style: none;
}

svg {
  display: block;
}

img {
  object-fit: cover;
  max-width: 100%;
  height: 100%;
  display: block;
}

a {
  cursor: pointer;
  text-decoration: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (width >= 768px) {
  .container {
    padding: 0 32px;
  }
}

@media screen and (width >= 1440px) {
  .container {
    padding: 0 24px;
  }
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-color);
  border-radius: 12px;
  height: 114px;
}

.header {
  z-index: 301;
  background-color: var(--header-background-color);
  border: 1.5px solid var(--primary-border-color);
  border-radius: 0 0 8px 8px;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 1440px;
  padding-top: 18px;
  padding-bottom: 18px;
  display: flex;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.header__navigation {
  display: none;
}

@media screen and (width >= 768px) {
  .header__navigation {
    justify-content: space-between;
    column-gap: 24px;
    display: flex;
  }

  .header__navigation-link {
    letter-spacing: -.56px;
    color: var(--denary-font-color);
    text-transform: uppercase;
    border-radius: 18px;
    flex-direction: row;
    align-items: center;
    padding: 8px 16px;
    font-family: DM Sans;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.3;
    transition: transform .3s, box-shadow .3s;
    display: flex;
  }

  .header__navigation-link:hover {
    transform: scale(1.1);
    box-shadow: 0 0 8px #0c0c0a;
  }

  .header__navigation-icon {
    fill: var(--secondary-icon-color);
    margin-left: 8px;
  }
}

.header__buttons {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.header__wrapper {
  flex-direction: row;
  align-items: center;
  column-gap: 40px;
  display: flex;
}

@media screen and (width >= 1440px) {
  .header__wrapper {
    column-gap: 76px;
  }
}

.logo {
  align-items: center;
  column-gap: 8px;
  transition: transform .3s;
  display: flex;
}

.logo:hover {
  transform: scale(1.1);
}

.logo__text {
  fill: var(--primary-font-color);
}

.theme-button {
  cursor: pointer;
  background-image: linear-gradient(#4f2ee8, #dcdcdc);
  border: none;
  border-radius: 18px;
  width: 40px;
  height: 20px;
  margin-right: 14px;
  padding: 1px;
  transition: background-image .3s;
}

.theme-button__icon {
  fill: var(--quaternary-object-background-color);
  background-color: var(--quaternary-object-background-color);
  border-radius: 50%;
  flex-shrink: 0;
  transition: transform;
  box-shadow: 1px 2px 4px #0000001a, 0 2px 2px #0000001a, 1px 1px 2px #0000001a;
}

body.dark-theme .theme-button__icon {
  transform: translateX(21px);
}

body.dark-theme .theme-button {
  background-image: linear-gradient(#4f2ee8 0%, #686868 100%);
}

.menu-toggle {
  background-color: inherit;
  cursor: pointer;
  border: none;
}

.menu-toggle__icon {
  fill: var(--secondary-icon-color);
  transition: transform 1s ease-out;
}

.menu-toggle__icon:hover {
  fill: var(--secondary-object-background-color);
}

.menu-toggle__icon:active {
  transform: rotate(380deg);
}

@media screen and (width >= 768px) {
  .menu-toggle {
    display: none;
  }
}

.login-buttons {
  position: relative;
}

.sign-up-btn {
  display: none;
}

@media screen and (width >= 768px) {
  .sign-up-btn {
    cursor: pointer;
    border: 1.5px solid var(--primary-border-color);
    letter-spacing: -.56px;
    background-color: var(--secondary-object-background-color);
    color: var(--quaternary-font-color);
    border-radius: 18px;
    column-gap: 90px;
    padding: 14px;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.3;
    display: flex;
  }
}

.sign-up-btn__icon {
  fill: var(--primary-object-background-color);
}

.log-out-btn {
  display: none;
}

@media screen and (width >= 768px) {
  .log-out-btn {
    background-color: var(--quaternary-object-background-color);
    color: var(--secondary-icon-color);
    border: 1px solid var(--secondary-icon-color);
    cursor: pointer;
    border-radius: 18px;
    justify-content: space-between;
    align-items: center;
    width: 184px;
    height: 48px;
    padding: 14px;
    font-size: 14px;
    font-weight: 500;
    display: flex;
    position: absolute;
  }
}

.log-out-btn__icon {
  fill: var(--primary-object-background-color);
}

.dark-theme .log-out-btn {
  color: #000;
}

.user-btn {
  display: none;
}

@media screen and (width >= 768px) {
  .user-btn {
    background-color: var(--secondary-object-background-color);
    cursor: pointer;
    border: none;
    border-radius: 36px;
    justify-content: space-between;
    align-items: center;
    width: 184px;
    height: 45px;
    padding: 0 12px;
    display: flex;
  }
}

.user-btn__img {
  border-radius: 50%;
}

.user-btn__userName {
  color: var(--quaternary-font-color);
  font-size: 20px;
  font-weight: 700;
  line-height: 1.4;
}

.user-btn__icon {
  fill: var(--primary-icon-color);
}

@media screen and (width >= 768px) {
  .visually-hidden {
    display: none;
  }
}

.js-open-menu:disabled, .js-close-menu:disabled {
  display: none;
}

.menu-container {
  z-index: 300;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  justify-content: center;
  width: 100vw;
  height: 100vh;
  margin-top: 60px;
  transition-property: transform;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container__navigation {
  z-index: 301;
  flex-direction: column;
  row-gap: 16px;
  display: flex;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.menu-container__navigation-link {
  color: var(--quinary-font-color);
  text-transform: uppercase;
  letter-spacing: -.56px;
  border-radius: 18px;
  padding: 8px 16px;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.3;
  display: inline-block;
}

.menu-container__navigation-link--shopping-list {
  flex-direction: row;
  align-items: center;
  column-gap: 4px;
  display: flex;
}

.menu-container__navigation-icon {
  fill: var(--primary-icon-color);
}

.menu-container__image {
  background-image: url("books-background@2x.aac83103.png");
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (width >= 768px) {
  .menu-container {
    display: none;
  }
}

.sign-up-mobile-btn {
  z-index: 301;
  border: 1.5px solid var(--primary-border-color);
  background-color: var(--secondary-object-background-color);
  color: var(--quaternary-font-color);
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  width: 118px;
  height: 48px;
  padding: 14px;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  position: absolute;
  top: 40px;
  left: 20px;
}

.sign-up-mobile-btn__icon {
  fill: var(--primary-object-background-color);
}

.log-out-mobile-btn {
  z-index: 301;
  background-color: var(--quaternary-object-background-color);
  color: var(--secondary-icon-color);
  border: 1px solid var(--secondary-icon-color);
  cursor: pointer;
  border-radius: 18px;
  justify-content: space-between;
  align-items: center;
  width: 118px;
  height: 48px;
  padding: 14px;
  font-size: 14px;
  font-weight: 500;
  display: none;
  position: absolute;
  bottom: 100px;
  left: 20px;
}

.log-out-mobile-btn__icon {
  fill: var(--primary-object-background-color);
}

.user-mobile-btn {
  z-index: 301;
  background-color: inherit;
  cursor: pointer;
  color: var(--quaternary-font-color);
  border: none;
  border-radius: 36px;
  align-items: center;
  column-gap: 9px;
  width: 184px;
  height: 45px;
  padding: 0 12px;
  display: none;
  position: absolute;
  top: 40px;
  left: 20px;
}

.user-mobile-btn__img {
  border-radius: 50%;
}

.user-mobile-btn__userName {
  font-size: 22px;
  font-weight: 700;
  line-height: 1.4;
}

.support {
  z-index: 0;
  background: radial-gradient(189.32% 190.93% at 59.76% -23.42%, var(--secondary-font-color) 18.03%, var(--quaternary-object-background-color) 100%);
  border-radius: 16px;
  width: 335px;
  max-width: 100%;
  padding: 24px 40px;
  list-style-type: none;
  box-shadow: 0 10px 10px #f5f5f71a;
}

@media screen and (width >= 1440px) {
  .support {
    width: 356px;
  }
}

.support__title {
  color: var(--quinary-font-color);
  letter-spacing: -.88px;
  align-items: center;
  gap: 12px;
  margin-left: auto;
  margin-right: auto;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
  display: flex;
}

@media screen and (width >= 768px) {
  .support__title {
    letter-spacing: -.96px;
    font-size: 24px;
    line-height: 28px;
  }
}

.support__icon-ua {
  fill: var(--primary-icon-color);
}

.support__list {
  flex-direction: column;
  gap: 20px;
  transition: transform .5s ease-in-out;
  display: flex;
}

.support__item {
  color: var(--quinary-font-color);
  flex-direction: row;
  align-items: center;
  gap: 16px;
  height: 32px;
  display: flex;
}

.support__link {
  display: flex;
}

.support__index {
  letter-spacing: -.56px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.support__icon-down {
  fill: #4f2ee8;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  display: flex;
}

.swiper {
  width: 149px;
  height: 292px;
  margin-top: 40px;
  margin-bottom: 20px;
  overflow: hidden;
  margin-left: 0 !important;
}

@media screen and (width >= 768px) {
  .swiper {
    margin-bottom: 24px;
  }
}

@media screen and (width >= 1440px) {
  .swiper {
    width: 159px;
  }
}

.next, .back {
  border: none;
  border-radius: 50%;
  place-items: center;
  width: 32px;
  height: 32px;
  margin: 0 auto;
  display: grid;
}

@media screen and (width >= 768px) {
  .next, .back {
    width: 38px;
    height: 38px;
  }
}

.btn-hidden {
  display: none;
}

.backdrop-popup {
  z-index: 550;
  background-color: var(--inactive-background-color);
  cursor: not-allowed;
  visibility: visible;
  opacity: 1;
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  inset: 0;
  transform: scale(1);
}

.popup-is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: scale(0);
}

.popup {
  border: 2px solid var(--senary-border-color);
  background-color: var(--quaternary-object-background-color);
  cursor: auto;
  border-radius: 18px;
  flex-direction: column;
  width: 100%;
  max-width: 335px;
  max-height: 100vh;
  margin: auto;
  padding: 40px 25px;
  display: flex;
  position: relative;
  overflow-y: auto;
}

.popup__btn-close {
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  display: flex;
  position: absolute;
  top: 12px;
  right: 12px;
}

.popup__icon-close {
  fill: var(--denary-font-color);
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.popup__icon-close:hover, .popup__icon-close:focus {
  fill: var(--secondary-font-color);
  cursor: pointer;
}

.popup__details {
  flex-direction: column;
  display: flex;
}

.popup__photo {
  border-radius: 8px;
  width: 100%;
  height: 408px;
  margin-right: 24px;
  overflow: hidden;
}

.popup__description {
  width: 100%;
}

.popup__title {
  text-transform: uppercase;
  letter-spacing: -.64px;
  margin-top: 14px;
  margin-bottom: 4px;
  font-size: 16px;
  line-height: 18px;
}

.popup__author {
  color: var(--tenth-font-color);
  letter-spacing: -.48px;
  font-size: 12px;
  font-style: italic;
  line-height: 14px;
}

.popup__text {
  letter-spacing: -.56px;
  margin: 18px 0;
  font-size: 14px;
  line-height: 18px;
}

.popup__shops {
  align-items: baseline;
  display: flex;
}

.popup__amazon-book {
  justify-content: space-between;
  align-items: center;
  width: 115px;
  display: flex;
}

.popup__amazon {
  background-image: url("amazon.2be9f61d.png");
  background-repeat: no-repeat;
  width: 62px;
  height: 20px;
}

.popup__icon-book {
  background-image: url("amazon-book.5a9f17e3.png");
  width: 32px;
  height: 32px;
}

.popup__shops-list {
  letter-spacing: -.56px;
  margin-left: 5px;
  font-size: 16px;
  font-weight: 500;
  line-height: 25px;
}

.popup__shops-link {
  color: orange;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.popup__shops-link:hover, .popup__shops-link:focus {
  color: var(--secondary-font-color);
}

.popup__btn-add, .popup__btn-remove {
  border: 2px solid var(--secondary-border-color);
  letter-spacing: -.14px;
  text-transform: uppercase;
  background-color: #0000;
  border-radius: 40px;
  margin: 40px auto 0;
  padding: 14px 23px;
  font-size: 13px;
  font-weight: 700;
  line-height: 18px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.popup__btn-add:hover, .popup__btn-add:focus, .popup__btn-remove:hover, .popup__btn-remove:focus {
  background-color: var(--secondary-object-background-color);
  color: var(--quaternary-font-color);
  cursor: pointer;
}

.popup__btn-text-hidden {
  display: none;
}

.popup__comment {
  text-align: center;
  color: var(--tertiary-font-color);
  letter-spacing: -.4px;
  width: 100%;
  max-width: 235px;
  margin: 8px auto 0;
  font-size: 10px;
  line-height: 12px;
}

.dark-theme .popup {
  background-color: var(--main-dark-background-color);
  border: 2px solid var(--quaternary-border-color);
  color: var(--quaternary-font-color);
}

.dark-theme .icon-close {
  fill: var(--quaternary-font-color);
}

.dark-theme .popup__amazon {
  background-image: url("amazon-white.dec59dbb.png");
}

.dark-theme .popup__btn-add, .dark-theme .popup__btn-remove {
  color: var(--quaternary-font-color);
}

.dark-theme .popup__comment {
  color: var(--nonary-font-color);
}

@media screen and (width >= 768px) {
  .popup {
    width: 100%;
    max-width: 579px;
    padding: 40px;
    position: relative;
  }

  .popup__btn-close {
    cursor: pointer;
    width: 28px;
    height: 28px;
    top: 20px;
    right: 20px;
  }

  .popup__details {
    flex-direction: row;
  }

  .popup__photo {
    border-radius: 8px;
    width: 192px;
    height: 281px;
    margin-right: 24px;
    overflow: hidden;
  }

  .popup__description {
    max-width: 279px;
  }

  .popup__title {
    letter-spacing: -.96px;
    margin-top: 0;
    margin-bottom: 8px;
    font-size: 24px;
    line-height: 28px;
  }

  .popup__author {
    letter-spacing: -.56px;
    font-size: 14px;
    line-height: 18px;
  }

  .popup__text {
    margin: 20px 0;
  }

  .popup__btn-add, .popup__btn-remove {
    letter-spacing: -.18px;
    width: 100%;
    max-width: 499px;
    padding: 20px 0;
    font-size: 18px;
    line-height: 24px;
  }

  .popup__comment {
    letter-spacing: -.48px;
    max-width: 324px;
    font-size: 12px;
    line-height: 14px;
  }
}

.registration-backdrop {
  background-color: var(--secondary-border-color);
  z-index: 400;
  z-index: 400;
  cursor: not-allowed;
  padding: 0 20px;
  transition: visibility .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  inset: 0;
}

.registration-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  transform: scale(0);
}

.registration-modal {
  border: 2px solid var(--primary-border-color);
  background-color: var(--main-background-color);
  border-radius: 18px;
  flex-direction: column;
  width: 100%;
  max-width: 335px;
  margin: auto;
  padding: 16px 20px 40px;
  display: flex;
  position: relative;
}

@media screen and (width >= 768px) {
  .registration-modal {
    max-width: 579px;
    padding: 52px 40px 40px;
  }
}

.registration-close-icon {
  pointer-events: none;
}

.registration-close-btn {
  cursor: pointer;
  stroke: var(--secondary-icon-color);
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  margin-left: auto;
  font-size: 16px;
  transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (width >= 768px) {
  .registration-close-btn {
    font-size: 18px;
    top: 24px;
    right: 24px;
  }
}

.registration-close-btn:hover {
  stroke: var(--secondary-font-color);
}

.registration-modal-field {
  margin-top: 24px;
}

@media screen and (width >= 768px) {
  .registration-modal-field {
    margin-top: 28px;
  }
}

.registration-modal-field-wrapper {
  align-items: center;
  display: flex;
  position: relative;
  top: 0;
  left: 0;
}

.registration-modal-icon {
  stroke: var(--denary-font-color);
  fill: var(--main-background-color);
  width: 18px;
  height: 18px;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  right: 32px;
  transform: translateY(-50%);
}

@media screen and (width >= 768px) {
  .registration-modal-icon {
    width: 24px;
    height: 24px;
  }
